const QRCode = require('qrcode.react')
const moment = require('moment-timezone')

export const RenderProducto: React.FC<{ productoPedido: ProductoPedido; movil: boolean }> = (props) => {
  const history = useHistory()
  const productoPedido = props.productoPedido
  const stNombre: React.CSSProperties = { fontSize: 14, fontWeight: 'bolder', color: 'var(--gris-oscuro-abi)' }
  const stCantidades: React.CSSProperties = { fontSize: 12, fontWeight: 'bolder', color: 'var(--gris-oscuro-abi)' }
  const stPrecio: React.CSSProperties = { fontSize: 10, fontWeight: 'bold', color: 'var(--gris-oscuro-abi)' }

  const Movil = props.movil
  const stContainer: React.CSSProperties = {
    display: 'flex',
    width: '100%',
    flexDirection: 'column',
    justifyContent: 'space-between',
    paddingLeft: '5%',
    height: 'fit-content',
    paddingRight: '5%',
    paddingBottom: 10,
    borderBottom: '1px solid var(--gris-claro-abi)',
    marginTop: 10,
  }
  return (
    <div style={stContainer}>
      <span style={stNombre}> {productoPedido.producto.nombre}</span>
      <a
        onClick={() =>
          history.push(
            RUTAS.PRODUCTO_DETAIL(productoPedido.producto.id, formatTextoURL(productoPedido.producto.nombre)),
          )
        }
      >
        <span style={{ color: 'var(--naranja-abi)', fontSize: Movil ? 10 : 14 }}>Ver detalles</span>
      </a>
      <span style={stCantidades}>Unidades: {productoPedido.cantidad}</span>
      <span style={stPrecio}>Precio total: {plata_fmt(productoPedido.total)}</span>
    </div>
  )
}

interface PedidoDetalleProps {
  pedido: PedidoCliente
  closeCallback: any
  Movil: boolean
}

export const PedidoDetalleSide: React.FC<PedidoDetalleProps> = observer((props) => {
  const rootStore = useStores()
  const history = useHistory()
  const { tarjetaStore, carritoStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [TextoLoading, setTextoLoading] = useState('Cargando..')
  const [Visible, setVisible] = useState(false)
  const [Pedido, setPedido] = useState(props.pedido)
  const [MedioPago, setMedioPago] = useState('')
  const [Contrasena, setContrasena] = useState('123654')
  const [CalificacionModalVisible, setCalificacionModalVisible] = useState(false)
  const [CalificacionTransportistaHabilitada, setCalificacionTransportistaHabilitada] = useState(false)
  const [CalificacionFabricanteHabilitada, setCalificacionFabricanteHabilitada] = useState(false)
  const [chatVisible, setChatVisible] = useState(false)
  const toaster = useToaster()
  const apiToaster = useApiToaster()

  useEffect(() => {
    setPedido(props.pedido)
  }, [props.pedido])

  const obtenerContrasenaPedido = (idPedido: number) => {
    rootStore.environment.api.getPedidoContrasena(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setContrasena(result.data.contrasena)
        setVisible(true)
      }
    })
  }

  const verificarCalificacion = (id_pedido: number, tipo_calificacion: number, id_calificar: number) => {
    rootStore.environment.api.verificar_calificacion(id_pedido, tipo_calificacion, id_calificar).then((result) => {
      if (result.kind === 'ok' && result.data) {
        const disponible = (result.data as any).disponible
        if (tipo_calificacion === 20) {
          setCalificacionFabricanteHabilitada(disponible)
        } else {
          setCalificacionTransportistaHabilitada(disponible)
        }
      }
    })
  }

  const seleccionarObjetivoCalificacion = (tipo_calificacion: number) => {
    if (Pedido) {
      let nombre = ''
      let imagen = ''
      let id_calificar = 0
      if (tipo_calificacion === 30) {
        // Transportista
        nombre = Pedido.transportista.usuario.first_name + ' ' + Pedido.transportista.usuario.last_name
        imagen = Pedido.transportista.usuario.imagen_perfil
        id_calificar = Pedido.transportista.id
      } else if (tipo_calificacion === 20) {
        // Fabricante
        const fabricante = Pedido.fabricante as any
        nombre = fabricante.nombre
        imagen = fabricante.imagen_perfil
        id_calificar = fabricante.id
      }
      const itinerarioCalificar = {
        tipo_calificacion: tipo_calificacion,
        id_pedido: Pedido.id,
        nombre: nombre,
        imagen: imagen,
        id_calificar: id_calificar,
      }
      rootStore.setPedidoCalificar(itinerarioCalificar)
      setCalificacionModalVisible(true)
    }
  }

  const obtenerPdf = (id: number, numero: string) => {
    rootStore.environment.api.getFacturaPdf(id).then((result) => {
      if (result.kind === 'ok') {
        if (Capacitor.getPlatform() === 'web') {
          FileDownload(result.data, numero + '.pdf')
        } else {
          const dwpath =
            Capacitor.getPlatform() === 'ios' ? `${File.documentsDirectory}` : `${File.externalRootDirectory}Download/`
          const dwpathReadable =
            Capacitor.getPlatform() === 'ios' ? `Documents/${numero}.pdf` : `Download/${numero}.pdf`

          const blobi = new Blob([result.data], { type: 'application/pdf' })
          File.writeFile(dwpath, `${numero}.pdf`, blobi, { replace: true })
            .then((res) => {
              toaster(`Factura descargada exitosamente, guardada en ${dwpathReadable}`)
            })
            .catch((error) => {
              toaster(`Error descargando factura, favor intentelo nuevamente!`, 'error')
            })
        }
      } else {
        apiToaster(result, '', 'descargar factura')
      }
    })
  }

  const repetirPedido = async () => {
    setTextoLoading('Agregando al carrito..')
    setLoading(true)
    const productos = [] as any
    const errores = [] as any
    const pdetalles = Pedido?.productos as any
    // Quitar productos del carrito
    for (let i = 0; i < carritoStore.productos.length; i++) {
      const producto = carritoStore.productos[i]
      await rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, 0, true).then((result) => {
        if (result.kind !== 'ok') {
          console.log('No se pudo quitar del carrito')
        }
      })
    }
    //
    for (let i = 0; i < pdetalles.length; i++) {
      const pdetalle = pdetalles[i]
      // Validar que haya en stock
      const prod = pdetalle.producto
      await rootStore.environment.api.productoDetalle(prod.id).then(async (result) => {
        if (result.kind !== 'ok') {
          errores.push(prod.nombre + ': ' + 'No está disponible')
          return
        }
        const producto = result.data as any
        if (producto.cantidad_disponible < pdetalle.cantidad) {
          errores.push(prod.nombre + ': ' + 'No hay suficiente cantidad en stock')
          return
        }
        productos.push(
          convertProductoCarrito({
            ...producto,
            cantidad: pdetalle.cantidad,
            fabricante: producto.fabricante.nombre,
            imagenes: producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '',
            categoria: producto.categoria.nombre,
          }),
        )
        await rootStore.environment.api
          .agregarEliminarProductoCarrito(producto.id, null, pdetalle.cantidad, false)
          .then((result) => {
            if (result.kind !== 'ok') {
              console.log('No se pudo agregar al carrito')
            }
          })
      })
    }
    carritoStore.setProductos(productos)
    if (errores.length) {
      setTimeout(() => alert('Los siguientes productos no se pudieron agregar:\n' + errores.join('\n')), 1)
    }
    setLoading(false)
    setTimeout(() => history.push(RUTAS.CARRITO), 500)
  }

  return (
    <>
      {Pedido && (
        <div id={'pedido-detalle-container'} style={{ display: 'flex', height: '100%' }}>
          <CalificacionTransportista
            color="var(--naranja-abi)"
            visible={CalificacionModalVisible}
            onAceptar={() => {
              setCalificacionModalVisible(false)
              setCalificacionModalVisible(false)
            }}
            onCerrar={() => {
              setCalificacionModalVisible(false)
            }}
          />
          <IonModal onDidDismiss={() => setVisible(false)} swipeToClose={true} backdropDismiss={true} isOpen={Visible}>
            <div className={'text-right'}>
              <a
                onClick={() => {
                  setVisible(false)
                }}
              >
                <FIcon.X />
              </a>
            </div>
            <div className={'text-center mb-lg-1 mb-0'}>
              <h2 className={'pb-5 mx-5'} style={{ color: 'black', lineHeight: 2 }}>
                Para recibir tu pedido debe mostrar el siguiente código al driver
              </h2>
              <QRCode value={Contrasena} size={150} />
              <h1 className={'pt-5'} style={{ color: 'black' }}>
                <strong>{Contrasena}</strong>
              </h1>
            </div>
            <div className={'mb-2 mx-2 p-2'}>
              <div className={'row'}>
                <div className={'col-12'}>
                  <button
                    className={'btn btn-primary btn-block'}
                    onClick={() => {
                      setVisible(false)
                    }}
                  >
                    Cerrar
                  </button>
                </div>
              </div>
            </div>
          </IonModal>
          <div className="container py-3 px-0 background-white">
            <button className={'row p-0 m-0 background-white'} style={{ color: 'gray' }} onClick={props.closeCallback}>
              <FIcon.ChevronRight size={25} style={{ color: 'gray' }} />
            </button>

            {Pedido.productos.map((producto) => (
              <RenderProducto key={producto.id} productoPedido={producto} movil={props.Movil} />
            ))}
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                paddingLeft: '5%',
                paddingRight: '5%',
                paddingBottom: 10,
                paddingTop: 10,
                gap: 10,
                borderBottom: '1px solid var(--gris-claro-abi)',
              }}
            >
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                <span> Mi Pedido</span>
                <span className="precios-pedido">{plata_fmt(Pedido.total - Pedido.envio)}</span>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                <span> Envío</span>
                <span className="precios-pedido">{plata_fmt(Pedido.envio)}</span>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
                  <span style={{ fontWeight: 'bold' }}> Total</span>
                  <span style={{ fontWeight: 'lighter', fontSize: '0.875rem' }}> Incluye IVA</span>
                </div>
                <span className="precios-pedido">{plata_fmt(Pedido.total)}</span>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                paddingLeft: '5%',
                paddingRight: '5%',
                paddingBottom: 10,
                paddingTop: 10,
                gap: 10,
              }}
            >
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'flex-start',
                  gap: 30,
                  marginBottom: 30,
                }}
              >
                <img
                  src={
                    !!Pedido.direccion.icono
                      ? !!ICONOS_DIRECCION[Pedido.direccion.icono]
                        ? ICONOS_DIRECCION[Pedido.direccion.icono]
                        : 'assets/icon/ubicacion-gray.png'
                      : 'assets/icon/ubicacion-gray.png'
                  }
                  style={{ height: 40 }}
                />
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
                  <span style={{ fontWeight: 'bold' }}> {Pedido.direccion?.nombre} </span>
                  <span style={{ fontWeight: 'lighter', fontSize: 12 }}>
                    {Pedido.direccion.calle_principal} {Pedido.direccion.numero} esq/{' '}
                    {Pedido.direccion.calle_secundaria}{' '}
                  </span>
                </div>
              </div>
              {/* <p className={'boton-card mb-0 px-3 py-2'}>
                <a onClick={() => setChatVisible(true)}>Contactar al driver</a>
              </p> */}
              <div>
                {Pedido.estado < PEDIDO_ESTADOS.entregado && (
                  <div className="finalizar-compra-container">
                    <button
                      className="btn btn-info px-5 py-2 mt-2"
                      type="button"
                      onClick={() => history.push(RUTAS.RASTREAR_PEDIDO(Pedido.id))}
                    >
                      Estado de tu pedido
                    </button>
                  </div>
                )}
                {Pedido.estado === PEDIDO_ESTADOS.entregado && (
                  <div className="finalizar-compra-container">
                    <button
                      className="btn btn-info px-5 py-2 mt-2"
                      type="button"
                      disabled={!!Pedido.calificacion_transportista}
                      onClick={() => seleccionarObjetivoCalificacion(30)}
                    >
                      Calificar Driver
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
          <ChatModal visible={chatVisible} setVisible={setChatVisible} pedido={Pedido} />
        </div>
      )}
    </>
  )
})

interface RenderPedidoProps {
  pedido: PedidoCliente
  sideDetailCallback: any
  pedidoSeleccionadoId?: number
}

export const RenderPedido: React.FC<RenderPedidoProps> = observer((props) => {
  const [pedido, setPedido] = useState<PedidoCliente>()
  const rootStore = useStores()
  const { carritoStore } = rootStore
  const addToast = useToaster()
  const Movil = /Mobi/.test(navigator.userAgent)
  const history = useHistory()
  const [Seleccionado, setSeleccionado] = useState(false)

  useEffect(() => {
    setPedido(props.pedido)
  }, [props.pedido])

  useEffect(() => {
    setSeleccionado(props.pedidoSeleccionadoId === props.pedido.id)
  }, [props.pedidoSeleccionadoId])

  const repetirPedido = async (pedido: PedidoCliente) => {
    addToast('Agregando al carrito..')
    const productos = [] as any
    const errores = [] as any
    const pdetalles = pedido.productos as any
    // Quitar productos del carrito
    for (let i = 0; i < carritoStore.productos.length; i++) {
      const producto = carritoStore.productos[i]
      await rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, 0, true).then((result) => {
        if (result.kind !== 'ok') {
          console.log('No se pudo quitar del carrito')
        }
      })
    }
    //
    for (let i = 0; i < pdetalles.length; i++) {
      const pdetalle = pdetalles[i]
      // Validar que haya en stock
      const prod = pdetalle.producto
      await rootStore.environment.api.productoDetalle(prod.id).then(async (result) => {
        if (result.kind !== 'ok') {
          errores.push(prod.nombre + ': ' + 'No está disponible')
          return
        }
        const producto = result.data as any
        if (producto.cantidad_disponible < pdetalle.cantidad) {
          errores.push(prod.nombre + ': ' + 'No hay suficiente cantidad en stock')
          return
        }
        productos.push(
          convertProductoCarrito({
            ...producto,
            cantidad: pdetalle.cantidad,
            fabricante: producto.fabricante.nombre,
            imagenes: producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '',
            categoria: producto.categoria.nombre,
          }),
        )
        await rootStore.environment.api
          .agregarEliminarProductoCarrito(producto.id, null, pdetalle.cantidad, false)
          .then((result) => {
            if (result.kind !== 'ok') {
              console.log('No se pudo agregar al carrito')
            }
          })
      })
    }
    carritoStore.setProductos(productos)
    if (errores.length) {
      setTimeout(() => alert('Los siguientes productos no se pudieron agregar:\n' + errores.join('\n')), 1)
    }
    setTimeout(() => history.push(RUTAS.CARRITO), 500)
  }

  return (
    <>
      {!!pedido && (
        <div
          style={{
            display: 'flex',
            width: '100%',
            justifyContent: 'space-between',
            paddingLeft: '5%',
            height: 'fit-content',
            textOverflow: 'ellipsis',
            paddingRight: '5%',
            paddingBottom: 10,
            paddingTop: 10,
            borderBottom: '1px solid var(--gris-claro-abi)',
            backgroundColor: Seleccionado ? 'var(--gris-mas-claro)' : 'white',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'column', flex: 2, paddingLeft: Movil ? 0 : 30 }}>
            <div style={{ overflow: 'hidden' }}>
              <span
                style={{
                  fontWeight: 'bold',
                  fontSize: Movil ? 12 : 15,
                  height: Movil ? 26 : 32,
                  textOverflow: 'ellipsis',
                }}
              >
                Pedido
                {pedido?.estado !== PEDIDO_ESTADOS.entregado && pedido?.estado !== PEDIDO_ESTADOS.incidente && (
                  <span> en curso</span>
                )}{' '}
                #{pedido.id}
              </span>
            </div>
            <div style={{ overflow: 'hidden' }}>
              <span
                style={{
                  fontWeight: 'bolder',
                  fontSize: Movil ? 12 : 15,
                  height: Movil ? 26 : 32,
                  textOverflow: 'ellipsis',
                  color: 'var(--gris-oscuro-abi)',
                }}
              >
                {moment(pedido.fecha).format('LLL')}
              </span>
            </div>

            <a
              onClick={() => {
                props.sideDetailCallback(pedido)
              }}
            >
              <span style={{ color: 'var(--naranja-abi)', fontSize: '0.875rem', fontWeight: 400 }}>Ver detalles</span>
            </a>
          </div>
          <div
            style={{
              flex: 1,
              display: 'flex',
              flexWrap: 'wrap',
              alignItems: 'center',
              justifyContent: 'flex-end',
              gap: 20,
            }}
          >
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'flex-end',
                flex: 1,
                minWidth: 100,
              }}
            >
              <div>
                <span style={{ color: 'var(--gris-claro-abi)', fontWeight: 400, fontSize: Movil ? 14 : 16 }}>
                  Total
                </span>
              </div>
              <div>
                <strong>{plata_fmt(pedido.total)}</strong>
              </div>
            </div>
            <div>
              <button
                className={'button-comprar-carrito-fav'}
                style={Movil ? {} : { width: 190 }}
                onClick={() => repetirPedido(pedido)}
              >
                <div>
                  <span className="carrito-pedido">{ICONOS.carrito}</span>
                  <span style={{ marginLeft: 5, fontSize: Movil ? 12 : 16, lineHeight: '20px' }}> Repetir compra</span>
                </div>
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  )
})

export const Pedido: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Pedidos, setPedidos] = useState<PedidoCliente[]>([])
  const [sideDetail, setSideDetail] = useState(false)
  const [pedidoDetail, setPedidoDetail] = useState<PedidoCliente>()
  const [Loading, setLoading] = useState(true)
  const sideDetailIntervalRef = useRef(null as any)
  const Movil = Capacitor.getPlatform() !== 'web'
  const CANTIDAD_PAGINA = 5
  const [CantidadMostrar, setCantidadMostrar] = useState(CANTIDAD_PAGINA)

  const sideDetailCallback = (pedido: PedidoCliente) => {
    setPedidoDetail(pedido)
    setSideDetail(true)
  }

  React.useEffect(() => {
    if (!sideDetail && sideDetailIntervalRef.current) {
      clearInterval(sideDetailIntervalRef.current)
      sideDetailIntervalRef.current = null
    } else if (sideDetail) {
      if (sideDetailIntervalRef.current) {
        clearInterval(sideDetailIntervalRef.current)
      }
      sideDetailIntervalRef.current = setInterval(() => {
        if (!pedidoDetail) {
          return
        }
        rootStore.environment.api.getPedido(pedidoDetail.id).then((result) => {
          if (result.kind === 'ok') {
            setPedidoDetail(result.pedido)
          }
        })
      }, 5000)
    }
  }, [sideDetail, pedidoDetail])

  React.useEffect(() => {
    obtenerPedidos()
    return () => {
      if (sideDetailIntervalRef.current) {
        clearInterval(sideDetailIntervalRef.current)
      }
    }
  }, [])

  const obtenerPedidos = () => {
    rootStore.environment.api.pedidos().then((result) => {
      if (result.kind === 'ok') {
        setLoading(false)
        setPedidos(result.pedidos)
      }
    })
  }

  return (
    <Page login_required={true} solo_cliente={true}>
      {/* Contenido de la página*/}
      <SubHeader links={[{ titulo: 'Mis Pedidos', ruta: RUTAS.PEDIDOS }]} Movil={Movil} />

      <div id={'pedido-container'} className="page-wrapper container-lg" style={{ display: 'flex', paddingTop: 0 }}>
        <div className="  px-0 background-white" style={{ flex: 2, paddingTop: 0 }}>
          <div style={{ display: 'flex' }}>
            <div style={{ flex: 1 }}>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  fontWeight: 'bold',
                  alignItems: 'center',
                  height: 60,
                }}
              >
                <strong>
                  <h3>Mis Pedidos</h3>
                </strong>
              </div>
              {Loading && (
                <div className="page-wrapper" style={{ display: 'flex', justifyContent: 'center' }}>
                  <IonSpinner />
                </div>
              )}
              {!Loading && Pedidos.length === 0 && (
                <div
                  style={{
                    flex: 1,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    height: '100%',
                    width: '100%',
                    flexDirection: 'column',
                  }}
                >
                  <div>
                    <span>Todavia no realizó pedidos en ABi.</span>
                    <br />
                  </div>
                  <a
                    style={{ width: 'fit-content', color: 'var(--rojo-abi)' }}
                    onClick={() => history.push(RUTAS.ROOT)}
                  >
                    Ver Productos
                  </a>
                </div>
              )}
              {Pedidos.length > 0 && (
                <>
                  {Pedidos.slice(0, CantidadMostrar).map((pedido) => {
                    return (
                      <RenderPedido
                        key={pedido.id}
                        pedido={pedido}
                        sideDetailCallback={(pedido: any) => sideDetailCallback(pedido)}
                        pedidoSeleccionadoId={pedidoDetail?.id}
                      />
                    )
                  })}
                </>
              )}
              <div className="d-flex justify-content-center m-5">
                {CantidadMostrar < Pedidos.length && (
                  <a
                    onClick={() => {
                      setCantidadMostrar(CantidadMostrar + CANTIDAD_PAGINA)
                    }}
                    style={{ fontWeight: 'bold', color: 'var(--naranja-abi)' }}
                  >
                    + Ver más
                  </a>
                )}
              </div>
            </div>
            {sideDetail && (
              <div id="pedido-detalle-wrapper" style={{ flex: 1 }}>
                {!!pedidoDetail && (
                  <PedidoDetalleSide Movil={Movil} closeCallback={() => setSideDetail(false)} pedido={pedidoDetail} />
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </Page>
  )
})

export default Pedido
